import React from 'react';
import '../../css/popup.css';

const Popup = props => {
  return (
    <div className="animate-fadeIn">
      <div className="popup__container center-vertically">
      </div>
        <div className="popup center-vertically animate-grow">
          <button className="close-mark btn btn-danger" onClick={() => props.onClose()}>
            <div>X</div>
          </button>
          <div className="popup__content center-vertically">{props.children}</div>
        </div>
    </div>
  );
};

export default Popup;
