import React, { useState } from 'react';
import SetsCalcForm from './setsCalcForm';
import SetContainer from './setContainer';
import Header from './common/header';
import Popup from './common/popup';
import '../css/setsCalculator.css';

const SetsCalculator = props => {
  const {
    workWeight,
    workNumReps,
    warmUpSets,
    percentages,
    unit,
    onSubmit,
    onLoad
  } = props;
  const contProps = { unit, onClick: onLoad };
  const workWeightContProps = {
    percentage: 1,
    weight: workWeight,
    numReps: workNumReps,
    isWorkingSet: true,
    btnText: 'Start'
  };
  const [showPopUp, setShowPopUp] = useState(false);
  const togglePopup = () => {
    setShowPopUp(!showPopUp);
  };

  return (
    <>
      <Header>
        <a
          href="https://www.t-nation.com/training/most-intelligent-way-to-warm-up"
          target="#blank"
        >
          <h1>Warm Up Sets Calculator</h1>
        </a>
      </Header>
      <SetsCalcForm
        unit={unit}
        onSubmit={onSubmit}
        onSettings={togglePopup}
        btnText="Calculate"
      />
      <div className="setList-container">
        <div className="setList mx-auto">
          {warmUpSets.map((set, index) => {
            const { percentage, weight, numReps } = set;
            const additionalProps = { percentage, weight, numReps };
            return (
              <SetContainer
                key={index}
                btnText="Load"
                {...contProps}
                {...additionalProps}
              />
            );
          })}
          {workWeight !== -1 && (
            <SetContainer {...contProps} {...workWeightContProps} />
          )}
        </div>
      </div>
      {showPopUp && (
        <Popup onClose={togglePopup}>
          {percentages.map((percentages, index) => {
            const { value, on } = percentages;
            return (
              <button
                onClick={() => props.togglePercentage(index)}
                className={`d-block mb-3 btn btn-${on ? 'success' : 'secondary'}`}
              >{`${Math.round(value * 100)}%`}</button>
            );
          })}
        </Popup>
      )}
    </>
  );
};

export default SetsCalculator;
